.page-header {
  padding-top: 0;
  margin-top: 0;
}

.page-header__wrapper {
  min-height: 50px;
  background-color: #ececec;
}

.page-header__list-wrapper {
  display: flex;
  justify-content: space-between;
}

.page-header__info {
  display: flex;
  justify-content: flex-start;
  min-height: 53px;
  padding-top: 33px;
  padding-left: 2px;
  margin-bottom: 23px;
}

.page-header__basket {
  position: relative;
  width: 190px;
  margin-top: 16px;
  margin-left: 136px;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: $main-color;
  text-align: right;

  &::before {
    top: -11px;
    left: 4px;
    width: 36px;
    height: 36px;
    background-image: url("../img/shopping-basket.svg");
    background-repeat: no-repeat;

    @include pseudo;
  }
}
