.slider-event {
  box-sizing: border-box;
  min-height: 420px;
  background-color: #9293a6;
}

.slider-event__title {
  padding-top: 48px;
  margin-top: 0;
  margin-bottom: 45px;
  font-size: 38px;
  font-weight: normal;
  line-height: 46px;
  color: #fff;
  text-align: center;
}

.swiper-slide-event {
  display: flex;
  justify-content: center;
}

.event__wrapper {
  position: absolute;
  top: 27px;
  right: 20px;
  width: 170px;
  text-align: left;
}

.event {
  position: relative;
}

.event img {
  width: 627px;
  height: 233px;
}

.event--margin-right {
  margin-right: 25px;
}

.swiper-slide-event__date {
  font-size: 14px;
  color: #9293a6;
}

.swiper-slide-event__title {
  margin-top: 1px;
  margin-bottom: 0;
  font-size: 18px;
  line-height: 25px;
}

.swiper-slide-event__text {
  margin-top: 7px;
  font-size: 14px;
  line-height: 20px;
}
