.swiper-slide-info {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  text-align: center;
}

.swiper-slide-info__height {
  display: flex;
  width: 405px;
  height: 580px;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
}

.swiper-slide-info__height img {
  width: 251px;
  height: 492px;
  margin-top: 34px;
  margin-left: -24px;
}

.swiper-slide-info__text-block {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin-top: 48px;
  margin-left: -7px;
  text-align: left;
}

.swiper-slide-info__height .swiper-slide-info__title {
  position: relative;
  margin-bottom: 35px;
  font-size: 38px;
  font-weight: normal;
  line-height: 20px;
  color: #0d0d14;

  &::after {
    bottom: -30px;
    left: 0;
    width: 165px;
    height: 1px;
    background-color: rgba(96, 98, 118, 0.2);

    @include pseudo;
  }
}

.swiper-slide-info__text {
  width: 169px;
  margin-bottom: 6px;
  font-size: 14px;
  line-height: 20px;
}

.swiper-slide-info__link {
  color: $main-color;
}

.swiper-slide-info__button {
  position: relative;
  width: 145px;
  height: 42px;
  padding-top: 3px;
  padding-left: 35px;
  margin-top: 33px;
  margin-left: 1px;
  font-family: "DaxlinePro", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #0d0d14;
  cursor: pointer;
  background-color: transparent;
  border: 3px solid $main-color;

  &::before {
    top: 8px;
    left: 14px;
    width: 22px;
    height: 19px;
    background-image: url("../img/shopping-basket-slider.svg");
    background-repeat: no-repeat;

    @include pseudo;
  }
}

.swiper-slide-info__double-slide {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 405px;
  height: 580px;
  margin-left: 32px;
}

.swiper-slide-info__wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 405px;
  height: 280px;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.15);
}

.swiper-slide-info__img-2 {
  width: 99px;
  height: 195px;
  padding-top: 31px;
  margin-left: 50px;
}

.swiper-slide-info__text-block-double-slide {
  position: absolute;
  right: -2px;
  width: 216px;
  height: 280px;
  margin-top: 0;
  margin-left: 42px;
  text-align: left;
  background-color: rgba(255, 255, 255, 0.9);
}

.swiper-slide-info__text-block-double-slide--color {
  color: #fff;
  background-color: $main-color;
}

.swiper-slide-info__wrapper .swiper-slide-info__title {
  position: relative;
  margin-top: 27px;
  margin-bottom: 30px;
  margin-left: 27px;
  font-size: 22px;
  font-weight: 500;
  line-height: 31px;

  &::after {
    bottom: -13px;
    left: 0;
    width: 165px;
    height: 1px;
    background-color: rgba(96, 98, 118, 0.2);

    @include pseudo;
  }
}

.swiper-slide-info__text-block-double-slide--color .swiper-slide-info__title {
  &::after {
    background-color: rgba(255, 255, 255, 0.2);
  }
}

.swiper-slide-info__wrapper .swiper-slide-info__text {
  margin-top: 0;
  margin-bottom: 20px;
  margin-left: 27px;
}

.swiper-slide-info__link--color {
  color: #fff;
}

.swiper-slide-info__button--double-slide {
  margin-top: 2px;
  margin-bottom: 28px;
  margin-left: 27px;
}

.swiper-slide-info__button--color {
  color: #fff;
  border: 3px solid #fff;

  &::before {
    background-image: url("../img/shopping-basket-slider-white.svg");
  }
}

.swiper-slide-info__img-5 {
  width: 65px;
  height: 161px;
  padding-top: 49px;
  margin-left: 64px;
}

.swiper-container-info__new {
  display: none;
}

.swiper-container-info__hits {
  display: none;
}

.swiper-container-info__new--active {
  display: block;
}

.swiper-container-info__hits--active {
  display: block;
}
