.callback {
  display: flex;
  justify-content: space-between;
  margin-left: 57px;
}

.callback-button {
  width: 188px;
  height: 50px;
  padding-top: 4px;
  font-family: "DaxlinePro", "Arial", sans-serif;
  font-size: 16px;
  font-weight: normal;
  line-height: 19px;
  background-color: #fff;
  border: 3px solid $main-color;
}

.callback-tel {
  width: 195px;
  margin-left: 25px;
}

.callback-number {
  margin-top: 0;
  margin-bottom: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 22px;
  font-weight: bold;
  line-height: 26px;
}
