.slider-news {
  box-sizing: border-box;
  min-height: 531px;
  padding-top: 51px;
  background-color: #dfe4eb;
}

.slider-news__title {
  margin-top: 0;
  margin-bottom: 39px;
  font-size: 38px;
  font-weight: normal;
  line-height: 46px;
  text-align: center;
}

.swiper-slide-news {
  display: flex;
  justify-content: center;
}

.swiper-slide-news__wrapper {
  width: 407px;
  margin-top: 16px;

  & img {
    margin-bottom: 26px;
  }
}

.swiper-slide-news__wrapper--width {
  width: 437px;
  height: 395px;
  margin-top: 0;
  background-color: #eef0f4;
}

.swiper-slide-news__img-1 {
  margin-left: -14px;
}

.swiper-slide-news__img-2 {
  margin-top: 16px;
  margin-left: 16px;
}

.swiper-slide-news__img-3 {
  margin-left: 15px;
}

.swiper-slide-news__text-block {
  box-sizing: border-box;
  padding-right: 16px;
}

.swiper-slide-news__text-block--1 {
  padding-left: 7px;
}

.swiper-slide-news__text-block--2 {
  padding-left: 37px;
}

.swiper-slide-news__text-block--3 {
  padding-left: 36px;
}

.swiper-slide-news__date {
  font-size: 14px;
  color: #9293a6;
}

.swiper-slide-news__title {
  margin-top: 2px;
  margin-bottom: 10px;
  font-size: 18px;
  line-height: 25px;
}

.swiper-slide-news__title-link {
  color: $main-color;
  text-decoration: none;
  background: linear-gradient($main-color, $main-color) no-repeat;
  background-position: 0 23px;
  background-size: 100% 2px;
}

.swiper-slide-news__title--line-height {
  margin-bottom: 15px;
  line-height: 22px;
}

.swiper-slide-news__text {
  margin-top: 0;
  line-height: 20px;
}
