.slider-ad {
  position: relative;
  min-height: 620px;
}

.swiper-slide-ad {
  overflow: hidden;
  border: none;
}

.swiper-slide-ad img {
  display: block;
  max-width: 1920px;
  height: 620px;
}

.swiper-slide__text-block {
  top: 89px;
  right: 50%;
  width: 515px;
  height: 375px;
  margin-right: 126px;

  @include pseudo;
}

.swiper-slide__title {
  position: relative;
  margin-bottom: 48px;
  font-size: 44px;
  font-weight: normal;
  line-height: 60px;
  color: 323232;
  text-align: left;

  &::after {
    bottom: -16px;
    left: 2px;
    width: 93px;
    height: 3px;
    background-color: $main-color;

    @include pseudo;
  }
}

.swiper-slide__text {
  width: 256px;
  margin-bottom: 65px;
  font-size: 16px;
  font-weight: 300;
  line-height: 22px;
  color: 323232;
  text-align: left;
}

.swiper-slide__button {
  width: 159px;
  height: 50px;
  margin-left: -354px;
  font-size: 16px;
  font-weight: normal;
  line-height: 19px;
  color: 323232;
  background-color: transparent;
  border: 3px solid $main-color;
}
