.site-list {
  position: relative;
  display: flex;
  justify-content: flex-start;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.site-list__item {
  position: relative;
  box-sizing: border-box;
  min-height: 70px;
  padding-top: 28px;
  padding-bottom: 10px;

  &:hover {
    background-color: #c22a2e;
  }
}

.site-list__item-link {
  padding-top: 25px;
  padding-bottom: 20px;
  font-size: 16px;
  font-weight: bold;
  line-height: 19px;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;

  &:hover {
    background-color: #c22a2e;
  }

  &--active {
    color: #faacae;
  }
}

.site-list__item:hover .submenu {
  cursor: pointer;
  opacity: 1;
  transform: scaleY(1);
}

.site-list__item-link--main {
  padding-right: 97px;
  padding-left: 0;
}

.site-list__item-link--company {
  position: relative;
  padding-right: 60px;
  // padding-right: 61px;
  // padding-left: 47px;
  padding-left: 47px;

  &::after {
    left: 163px;

    @include site-list__after;
  }
}

.site-list__item-link--catalog {
  position: relative;
  padding-right: 68px;
  padding-left: 78px;

  &::after {
    left: 233px;

    @include site-list__after;
  }
}

.site-list__item-link--expert {
  position: relative;
  padding-right: 70px;
  padding-left: 70px;

  &::after {
    left: 192px;

    @include site-list__after;
  }
}

.site-list__item-link--patient {
  position: relative;
  padding-right: 83px;
  padding-left: 73px;

  &::after {
    left: 172px;

    @include site-list__after;
  }
}

.site-list__item-link--contact {
  padding-right: 0;
  padding-left: 55px;
}
