.user-list {
  display: flex;
  justify-content: flex-start;
  padding-left: 0;
  margin-top: 0;
  margin-bottom: 0;
  list-style: none;
}

.user-list__item {
  margin-top: 18px;
  margin-right: 21px;
  margin-bottom: 17px;
}

.user-list__item--border {
  padding-right: 20px;
  border-right: 1px solid #a6a7b1;
}

.user-list__item a {
  display: inline-block;
  line-height: 14px;
  color: #a6a7b1;
  text-decoration: none;
  border-bottom: 1px solid #a6a7b1;
}
