.swiper-button-arrow {
  fill: $main-color;

  &:hover {
    fill: #323232;
  }
}

.swiper-button-arrow--slider-event {
  fill: #fff;
}

.swiper-button-prev,
.swiper-container-rtl {
  right: 50%;
  margin-right: 680px;
}

.swiper-button-next,
.swiper-container-rtl {
  left: 50%;
  margin-left: 680px;
}
