.footer {
  box-sizing: border-box;
  min-height: 390px;
  padding-top: 65px;
  color: #fff;
  background-color: #2a3236;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
}

.footer__copyright {
  width: 178px;
  padding-top: 2px;
  margin-right: 37px;
}

.footer__logo-link {
  margin-left: 2px;
}

.footer__copyright-text {
  width: 200px;
  margin-top: 29px;
  margin-left: 2px;
  font-size: 16px;
  line-height: 19px;

  & span {
    display: block;
    line-height: 19px;
  }
}

.footer__logo-link--text-img {
  display: block;
  margin-top: 5px;
  margin-left: -8px;
}

.footer__nav {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}

.footer__list-wrapper {
  width: 200px;
  margin-right: 20px;

  &:last-child {
    margin-right: 0;
  }
}

.footer__title {
  margin-top: 0;
  margin-bottom: 35px;
  font-size: 16px;
  font-weight: bold;
  line-height: 20px;
}

.footer__list {
  padding-left: 0;
  list-style: none;
}

.footer__item {
  font-size: 16px;
  line-height: 26px;

  & a {
    color: #fff;
    text-decoration: none;
  }
}

.footer__button {
  margin-top: 11px;
  margin-bottom: 17px;
  color: #fff;
  background-color: transparent;
}

.footer__contacts {
  width: 200px;
  margin-left: 10px;
  text-align: right;
}

