.product {
  box-sizing: border-box;
  min-height: 187px;
  padding-top: 25px;
}

.product__list {
  box-sizing: border-box;
  display: flex;
  justify-content: flex-start;
  padding: 0;
  padding-left: 26px;
  list-style: none;
}

.product__list-item a {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  font-size: 16px;
  font-weight: bold;
  line-height: 40px;
  text-decoration: none;
}

.product__img {
  height: 85px;
  padding-bottom: 12px;
}

.product__list-class {
  padding-left: 10px;
}

.product__list-item:first-child {
  margin-right: 80px;

  & span {
    color: #9ebf56;
  }
}

.product__list-item:nth-child(2) {
  position: relative;
  margin-right: 46px;

  & span {
    color: #e2c058;
  }

  & .product__list-class {
    color: #fff;
  }

  &::after {
    right: -3px;
    background-color: #e2c058;

    @include product__list-class__after;
  }
}

.product__list-item:nth-child(3) {
  position: relative;
  margin-right: 37px;

  & span {
    color: #c0568a;
  }

  & .product__list-class {
    color: #fff;
  }

  &::after {
    right: -7px;
    background-color: #c0568a;

    @include product__list-class__after;
  }
}

.product__list-item:nth-child(4) {
  position: relative;
  margin-right: 38px;

  & span {
    color: #c0568a;
  }

  & .product__list-class {
    color: #fff;
  }

  &::after {
    right: -2px;
    background-color: #c0568a;

    @include product__list-class__after;
  }
}

.product__list-item:nth-child(5) {
  margin-right: 62px;

  & span {
    color: #b0b0b0;
  }
}

.product__list-item:nth-child(6) {
  position: relative;
  margin-right: 86px;

  & span {
    color: #b0b0b0;
  }

  & .product__list-class {
    color: #fff;
  }

  &::after {
    right: -11px;
    background-color: #b0b0b0;

    @include product__list-class__after;
  }
}

.product__list-item:nth-child(7) {
  margin-right: 100px;

  & span {
    color: #6fbbcf;
  }
}

.product__list-item:last-child {
  position: relative;

  & span {
    color: #6fbbcf;
  }

  & .product__list-class {
    color: #fff;
  }

  &::after {
    right: -5px;
    background-color: #6fbbcf;

    @include product__list-class__after;
  }
}
