.slider-info {
  box-sizing: border-box;
  min-height: 870px;
  padding-top: 40px;
  background-color:#f0f0f0;
}

.slider-info__tabs {
  box-sizing: border-box;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  width: 493px;
  padding-right: 21px;
  padding-bottom: 29px;
  padding-left: 37px;
}

.slider-info__title {
  font-size: 28px;
  font-weight: normal;
  line-height: 21px;
  color: $main-color;
  cursor: pointer;
  border-bottom: 1px dashed $main-color;

  &:active {
    transform: scale(0.95);
  }
}

.slider-info__title--active {
  font-size: 38px;
  color: #0d0d14;
  border-bottom: none;
}
