@font-face {
  font-family: "DaxlinePro";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/DaxlinePro-Regular.woff2") format("woff2"), url("../fonts/DaxlinePro-Regular.woff") format("woff");
}

@font-face {
  font-family: "DaxlinePro";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/DaxlinePro-Bold.woff2") format("woff2"), url("../fonts/DaxlinePro-Bold.woff") format("woff");
}

body {
  min-width: 320px;
  padding: 0;
  margin: 0;
  font-family: "DaxlinePro", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  line-height: 14px;
  color: #323232;
  background-color: #fff;
}

.container {
  width: 1281px;
  margin: 0 auto;
}

