.about-company {
  box-sizing: border-box;
  min-height: 496px;
}

.about-company__title {
  padding-top: 68px;
  margin-top: 0;
  margin-bottom: 50px;
  font-size: 38px;
  font-weight: normal;
  color: #0d0d14;
  text-align: center;
}

.about-company__block {
  display: flex;
  justify-content: flex-start;
}

.about-company__block-video {
  margin-top: 36px;
  margin-right: 58px;
}

.about-company__text {
  width: 430px;
  margin-top: 30px;
  margin-right: 6px;
  font-size: 24px;
  line-height: 31px;

  & span {
    color: $main-color;
  }
}

.advantages {
  width: 406px;
  margin-top: 8px;
  line-height: 19px;

  & p {
    margin-bottom: 12px;
    font-size: 16px;
  }
}

.advantages__list {
  padding-left: 43px;
  margin-top: 0;
  list-style: none;
}

.advantages__item {
  position: relative;
  margin-bottom: 5px;
  font-size: 16px;
  line-height: 17px;

  &::before {
    top: 4px;
    left: -32px;
    width: 7px;
    height: 7px;
    background-color: $main-color;
    border-radius: 50%;

    @include pseudo;
  }
}
