.submenu {
  position: absolute;
  z-index: 5;
  width: 100%;
  padding-left: 0;
  margin-top: 22px;
  list-style: none;
  background: #c22a2e;
  opacity: 0;
  transition: 0.2s ease-in-out;
  transform: scaleY(0);
  transform-origin: 0 0;
}

.submenu a {
  display: block;
  padding-left: 36px;
  font-size: 16px;
  line-height: 39px;
  color: #fff;
  text-decoration: none;

  &:hover {
    background-color: #e33439;
  }
}

.submenu__item:last-child a {
  min-height: 53px;
}
